          <div class="support-title">H TR TRC TUYN</div>
          <div class="support-hotline mb-3">
            <span>
              <i class="fa fa-phone text-danger"></i>
              <strong class="text-danger">HOTLINE: 0963 888 912</strong>
            </span>
          </div>
          <div class="support-contact">
            <div class="mb-3">
              <strong>T vn bn hng</strong><br />
              <i class="fa fa-phone"></i> 0963 888 912<br />
              <i class="fa fa-envelope"></i> sonandatex.com.vn
            </div>
            <hr />
            
          </div>
        </div>
      </div>

      <!-- Ct gii thiu -->
      <div class="col-lg-9 col-md-8 col-12">
        <div class="intro-content shadow-sm h-100 bg-white p-4 rounded text-justify">
          <p>
            <strong>ANDATEX</strong>  Hng sn hng u vi cht lng v a dng sn phm sn cao cp.
          </p>
          <p>
            L mt cng ty hng u trong lnh vc sn xut sn, vi danh ting mnh m v s tin cy cao trn th trng.
            Nhiu nm kinh nghim, ANDATEX  khng nh c v th ca mnh bng vic cung cp cc sn phm sn cht lng cao v a dng.
            Vi s tn tm v chuyn nghip, chng ti t cht lng l u tin hng u.
          </p>
          <p>
            ANDATEX cung cp mt lot cc loi sn, bao gm sn ni tht, sn ngoi tht, sn cng nghip v sn chng chy, p ng c cc yu cu s dng a dng.
          </p>
          <p>
            Lin h <strong>0963 888 912</strong>  nhn t vn v u i t ANDATEX.
          </p>
        </div>
      </div>
    </div>
  </div>
</section>

  <section >
    <div class="container mt-5">
      <div v-if="products && products.length">
        <ProductCate
          v-for="category in categories"
          :key="category.id"
          :products="filteredProducts(category.id)"
          :title="category.tenDanhMuc"
          :nameDanhMuc="slugify(category.tenDanhMuc)"
        />
      </div>
      <ProductDetail :product="selectedProduct" v-if="selectedProduct" />
    </div>
    <Layout_Footer_SonAndatex></Layout_Footer_SonAndatex>
  </section>
  <FloattingContact></FloattingContact>
</template>

<script>
import axios from "axios";

import ProductDetail from "@/components/Product/ProductDetail.vue";
import ProductCate from "../Product/ProductCate.vue";

import { apiGetSanPham, apiGetDanhMuc } from "@/assets/js/api";
import Layout_Menu_SonAndatex from "../view/layout_Menu_SonAndatex.vue";
import Layout_Footer_SonAndatex from "../view/layout_Footer_SonAndatex.vue";
import FloattingContact from "../view/FloattingContact.vue";
import { header } from "@/assets/js/snapService";

export default {
  name: "ProductList",
  components: {
    Layout_Footer_SonAndatex,
    Layout_Menu_SonAndatex,
    ProductDetail,
    FloattingContact,
    ProductCate,
  },
  data() {
    return {
      products: [],
      selectedProduct: null,
      categories: [
        // { id: 1, name: "SN NI THT" },
        // { id: 2, name: "SN NGOI THT" },
        // { id: 3, name: "SN LT" },
        // { id: 4, name: "SN CHNG THM" },
        // { id: 5, name: "SN BT B - SN NH" },
      ],
    };
  },
  created() {
    this.getProducts();
    this.fetchDanhMuc();
  },
  methods: {
    slugify(name) {
      return name
        .toLowerCase()
        .normalize("NFD")
        .replace(/[\u0300-\u036f]/g, "") // xo du ting Vit
        .replace(/\s+/g, "-");
    },

    fetchDanhMuc() {
      let data = {
        funcId: 10,
      };
      axios
        .post(apiGetDanhMuc, data, {
          headers: header,
        })
        .then((response) => {
          console.log(response.data);
          this.categories = response.data.data;
          console.log("this.danhMuc ", this.categories);
        })
        .catch((error) => {
          console.error("Error fetching product:", error);
        });
    },

    getProducts() {
      let data = {
        funcId: 10,
      };
      axios
        .post(apiGetSanPham, data, { headers: header })
        .then((res) => {
          console.log("Danh sch sn phm:", res.data.data);

          this.products = res.data.data;
        })
        .catch((err) => {
          console.error("Li ti sn phm:", err);
        });
    },
    filteredProducts(categoryId) {
      return this.products.filter((product) => product.idDanhMuc === categoryId);
    },
    goToProductDetal(product) {
      this.selectedProduct = product;
    },
  },
};
</script>

