<template>
  <main>
    <div class="table-data">
      <!-- Danh sch sn phm -->
      <div class="todo">
        <div class="head">
          <h3>Sn phm</h3>
        </div>
        <ul class="todo-list" style="height: 700px; overflow: auto">
          <li
            v-for="(itemSanPham, index) in products"
            :key="index"
            class="completed"
            style="background: aliceblue"
            @click="editProduct(itemSanPham)"
          >
            <img
              :src="'https://sonandatex.com.vn/uploads/' + itemSanPham.imgSP"
              alt="nh sn phm"
              style="width: 50px; height: 50px"
            />
            <strong>{{ itemSanPham.tenSP }}</strong>
            <span
              class="ml-lg-2 cursor-pointer"
              @click.stop="deleteProduct(itemSanPham.id)"
              style="cursor: pointer"
            >
              <i
                class="fa fa-times-circle"
                style="color: red"
                aria-hidden="true"
              ></i>
            </span>
          </li>
        </ul>
      </div>

      <!-- Chi tit sn phm -->
      <div class="order">
        <div class="head">
          <h3>Chi tit</h3>
        </div>

        <div
          class="form-group input-group d-flex"
          style="flex-direction: column"
        >
          <span class="has-float-label d-none">
            <input
              class="form-control"
              type="text"
              v-model="form.id"
              placeholder=" "
            />
          </span>
          <div class="container">
            <div class="row">
              <!-- Tn sn phm -->
              <span class="has-float-label col-12 col-md-6 p-1">
                <input
                  class="form-control"
                  :class="{ 'is-invalid': errors.tenSP }"
                  type="text"
                  v-model="form.tenSP"
                  placeholder=" "
                />
                <label>Tn sn phm</label>
                <small v-if="errors.tenSP" class="text-danger">{{
                  errors.tenSP
                }}</small>
              </span>
              <!-- URL nh hin th -->
              <span class="has-float-label col-12 col-md-6 p-1">
                <input
                  class="form-control"
                  :class="{ 'is-invalid': errors.maSP }"
                  type="text"
                  v-model="form.maSP"
                  placeholder=" "
                />
                <label>M sn phm</label>
                <small v-if="errors.maSP" class="text-danger">{{
                  errors.maSP
                }}</small>
              </span>
            </div>
          </div>
          <!-- URL nh hin th -->
          <span class="has-float-label mt-3 p-1">
            <input
              class="form-control"
              type="text"
              v-model="form.mota"
              placeholder=" "
            />
            <label>Chi tit</label>
          </span>
          <!-- URL nh hin th -->
          <span class="has-float-label mt-3 p-1">
            <input
              class="form-control"
              type="text"
              v-model="form.imgSP"
              :class="{ 'is-invalid': errors.imgSP }"
              placeholder=" "
            />
            <label>URL nh hin th</label>
            <small v-if="errors.imgSP" class="text-danger">{{
              errors.imgSP
            }}</small>
          </span>

          <!-- Danh mc sn phm -->
          <span class="has-float-label mt-3 p-1">
            <select
              class="form-control"
              :class="{ 'is-invalid': errors.idDanhMuc }"
              v-model="form.idDanhMuc"
              @change="onCategoryChange"
            >
              <option value="" disabled>Chn danh mc</option>
              <option value="1">Sn ni tht</option>
              <option value="2">Sn ngoi tht</option>
              <option value="3">Sn lt</option>
              <option value="4">Sn chng thm</option>
              <option value="5">Sn bt b - sn nh</option>
            </select>
            <label>Danh mc sn phm</label>
            <small v-if="errors.idDanhMuc" class="text-danger">{{
              errors.idDanhMuc
            }}</small>
          </span>
        </div>
        <div class="container">
          <div
            class="row mt-4"
            v-for="(item, index) in quyCach"
            :key="item.id || index"
          >
            <!-- Quy cch -->
            <span class="has-float-label col-12 col-md-4 p-1">
              <input
                class="form-control"
                type="text"
                v-model="item.quyCach"
                placeholder=" "
              />
              <label>Quy cch</label>
            </span>

            <!-- Gi bn -->
            <span class="has-float-label col-12 col-md-4 p-1">
              <input
                class="form-control"
                type="number"
                v-model="item.giaBan"
                placeholder=" "
              />
              <label>Gi bn</label>
            </span>

            <!-- nh mc + nt xo -->
            <span class="has-float-label col-12 col-md-4 p-1 d-flex">
              <input
                class="form-control"
                type="text"
                v-model="item.dinhMuc"
                placeholder=" "
              />
              <label>nh mc <i>(m2/thng)</i></label>
              <button
                class="ml-2"
                style="
                  cursor: pointer;
                  background-color: beige;
                  border: none;
                  border-radius: 10px;
                  height: 38px;
                  width: 38px;
                  display: flex;
                  align-items: center;
                  justify-content: center;
                "
                @click="removeQuyCach(index)"
              >
                <i
                  class="fa fa-times-circle"
                  style="color: red"
                  aria-hidden="true"
                ></i>
              </button>
            </span>
          </div>

          <!-- Nt thm mi 1 dng quy cch -->
          <button class="btn btn-add mt-3 m-auto" @click="addQuyCach">
            Thm mc mi
          </button>
        </div>

        <!-- Nt hnh ng -->
        <div class="d-flex">
          <button
            class="w-25 btn btn-primary"
            @click="saveProduct"
            style="height: 50px"
          >
            {{ form.id ? "Cp nht sn phm" : "Thm sn phm" }}
          </button>
          <button
            class="w-25 btn btn-secondary ml-2"
            @click="resetForm"
            style="height: 50px"
          >
            Hy
          </button>
        </div>

        <!-- Upload nh -->
        <!-- <div class="mt-5">
          <input
            type="file"
            @change="onFileChange"
            accept="image/*"
            class="form-control"
          />
          <div v-if="previewImage" class="mt-3 d-flex flex-column align-items-center">
            <img
              :src="previewImage"
              alt="Preview"
              style="max-width: 50%; height: auto"
            />
            <input :value="urlImage" type="text" class="form-control mt-2" /> 
          </div>
          <button
            class="btn btn-success mt-3"
            @click="uploadImage"
            :disabled="!selectedFile"
          >
            Ti ln nh
          </button>
        </div>

        <div v-for="(img, index) in uploadedImages" :key="index" c>
  <img :src="img" style="max-width: 100px; margin: 10px;" />
</div>
      </div> -->
      </div>
    </div>
  </main>
</template>

<script>
import axios from "axios";
import { apiGetImg, apiGetSanPham, apiUploadImg } from "@/assets/js/api"; // hoc bn thay bng ng dn c nh
import { header } from "@/assets/js/snapService";

export default {
  data() {
    return {
      products: [],
      form: {
        id: null,
        tenSP: "",
        imgSP: "",
        idDanhMuc: "",
        maSP: "",
        mota: "",
      },
      quyCach: [],
      errors: {},
      urlImage: "",
      selectedFile: null,
      previewImage: null,
      uploadedImages: [],
    };
  },

  mounted() {
    this.getProducts();
    this.fetchUploadedImages(); // Ly danh sch nh  ti ln
  },

  methods: {
    //  Ly danh sch sn phm
    getProducts() {
      let data = {
        funcId: 10,
      };
      axios
        .post(apiGetSanPham, data, { headers: header })
        .then((res) => {
          console.log("Danh sch sn phm:", res.data.data);
          this.products = res.data.data;
        })
        .catch((err) => {
          console.error("Li ti sn phm:", err);
        });
    },

    //  Lu sn phm (thm mi hoc cp nht)
    async saveProduct() {
      // Reset li
      this.errors = {};

      // Kim tra cc trng bt buc
      if (!this.form.tenSP)
        this.errors.tenSP = "Tn sn phm khng c  trng";
      if (!this.form.maSP) this.errors.maSP = "M sn phm khng c  trng";
      if (!this.form.idDanhMuc)
        this.errors.idDanhMuc = "Danh mc khng c  trng";
      if (!this.form.imgSP) this.errors.imgSP = "Link nh khng c  trng";

      // Nu c li, dng li
      if (Object.keys(this.errors).length > 0) {
        alert("Vui lng kim tra li cc trng bt buc");
        return;
      }

      const isUpdate = !!this.form.id;
      const funcId = isUpdate ? 13 : 12;

      const payload = {
        funcId,
        ...this.form,
        quyCach: this.quyCach,
      };

      try {
        const response = await axios.post(apiGetSanPham, payload);
        alert(response.data.message || "Thao tc thnh cng");

        this.getProducts(); // Lm mi danh sch
        this.resetForm(); // Reset li form
      } catch (err) {
        console.error("Li thao tc:", err);
        alert("C li xy ra khi lu sn phm");
      }
    },

    //  Xa sn phm
    async deleteProduct(id) {
      if (!confirm("Bn c chc mun xa sn phm ny?")) return;

      try {
        const response = await axios.post(apiGetSanPham, {
          funcId: 14,
          id,
          type: 1, // 1 l xa sn phm
        });
        alert(response.data.message || " xa");

        this.getProducts(); // Lm mi danh sch
        if (this.form.id === id) this.resetForm();
      } catch (err) {
        console.error("Li xa sn phm:", err);
        alert("Xa tht bi");
      }
    },

    //  Chnh sa sn phm
    async editProduct(product) {
      this.form = { ...product };
      await this.fetchProductDetal(product.id);
    },

    //  Ly quy cch theo id sn phm
    async fetchProductDetal(id) {
      try {
        const response = await axios.post(apiGetSanPham, {
          funcId: 17,
          id: id,
        });

        this.quyCach = response.data.data[0].quycach || [];
      } catch (error) {
        console.error("Error:", error);
      }
    },
    //  Thm dng quy cch
    addQuyCach() {
      this.quyCach.push({
        id: null,
        idSanPham: this.form.id || null,
        quyCach: "",
        giaBan: "",
        dinhMuc: "",
      });
    },

    //  Xo dng quy cch
    removeQuyCach(index) {
      this.quyCach.splice(index, 1);
    },

    //  Reset form
    resetForm() {
      this.form = {
        id: null,
        tenSP: "",
        imgSP: "",
        idDanhMuc: "",
        maSP: "",
        mota: "",
      };
      this.quyCach = [];
    },

    onFileChange(e) {
      const file = e.target.files[0];
      this.selectedFile = file;
      this.previewImage = URL.createObjectURL(file);
    },

    async uploadImage() {
      if (!this.selectedFile) return;

      const formData = new FormData();
      formData.append("image", this.selectedFile); // Ging nh key trong Postman

      try {
        const response = await axios.post(apiUploadImg, formData, {
          headers: {
            "Content-Type": "multipart/form-data",
          },
        });

        console.log(" Ti ln thnh cng:", response.data);
        this.form.imgSP = response.data.imageUrl; // Cp nht ng dn nh vo form sn phm
      } catch (error) {
        console.error(" Li ti nh:", error);
      }
    },
    async fetchUploadedImages() {
      try {
        const res = await axios.get(apiGetImg);
        this.uploadedImages = res.data.images;
        console.log("Danh sch nh:", this.uploadedImages);
      } catch (error) {
        console.error("Li ly danh sch nh:", error);
      }
    },
  },
};
</script>

<style scoped>
.btn-add {
  color: #fff;
  background-color: #14a1ec;
  border-color: #28a745;
}
.container {
  margin-top: 20px;
}

.todo-list {
  list-style-type: none;
  padding: 0;
}

.todo-list li {
  margin-bottom: 10px;
}

.has-float-label {
  position: relative;
}

.has-float-label input,
.has-float-label select {
  width: 100%;
  padding: 10px 10px 10px 5px;
  font-size: 16px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.has-float-label label {
  position: absolute;
  top: 50%;
  left: 10px;
  transform: translateY(-50%);
  transition: all 0.2s ease-in-out;
  color: #aaa;
  pointer-events: none;
}

.has-float-label input:focus + label,
.has-float-label input:not(:placeholder-shown) + label,
.has-float-label select:focus + label,
.has-float-label select:not([value=""]) + label {
  top: -10px;
  font-size: 12px;
  color: #333;
}

.has-float-label input::placeholder {
  color: #ccc;
  opacity: 1;
}

.d-flex {
  display: flex;
  justify-content: space-between;
  /* margin-top: 20px; */
}
.is-invalid {
  border-color: red;
}
.text-danger {
  color: red;
  font-size: 12px;
}
button {
  cursor: pointer;
}
</style>
