<script>
import LayoutMenuSonAndatex from "../view/layout_Menu_SonAndatex.vue";
import LayoutFooterSonAndatex from "../view/layout_Footer_SonAndatex.vue";
import { imageUrls } from "@/assets/js/imgUrl.js";
imageUrls;
export default {
  name: "GioiThieu",
  components: {
    LayoutMenuSonAndatex,
    LayoutFooterSonAndatex,
  },
  data() {
    return {
      imageUrls,
    };
  },
  mounted() {
    // Intersection Observer cho animation khi scroll
    const observer = new IntersectionObserver(
      (entries) => {
        entries.forEach((entry) => {
          if (entry.isIntersecting) {
            entry.target.classList.add("show");
            observer.unobserve(entry.target); // ch chy 1 ln
          }
        });
      },
      { threshold: 0.2 }
    );

    document.querySelectorAll(".animate").forEach((el) => observer.observe(el));
  },
};
</script>

