        </h1>
      </div> -->
      <tu-van-free></tu-van-free>
    </section>
  </main>

  <!-- Section gii thiu Vinalux -->
  <section
    class="vinalux-section container mt-4"
    aria-labelledby="vinalux-title"
  >
    <div class="row align-items-center">
      <div class="col-12 col-md-6 animate" data-animate="fade-right">
        <figure class="vinalux-left">
          <img
            src="/img/banner/BM2.png"
            alt="Bng mu sn Vinalux"
            class="vinalux-img"
            loading="lazy"
          />
        </figure>
      </div>

      <div class="col-12 col-md-6 animate" data-animate="fade-left">
        <div class="vinalux-right">
          <h2 id=" text-start vinalux-title" class="vinalux-title gradient-text ">
           BNG MU SN ANDATEX
          </h2>
          <p class="vinalux-desc text-justify" >
            Hy khm ph b su tp bng mu sn ANDATEX vi s pha trn tinh t
            ca hn 500 sc thi mu sc, t nhng gam mu pastel nh nhng,
            xanh bin mt lnh, vng rc r, cam m p n  ni bt v xanh l
            ti mi. Mi mu sc l mt cm hng sng to, gip bn d dng
            phi hp v bin ha khng gian sng theo phong cch ring, t hin
            i tr trung n sang trng c in.<br />
           
          </p>

          <strong class="text-start">H thng bng mu:</strong>
          <div class="vinalux-color-list animate" data-animate="zoom-in">
            <div
              class="vinalux-color"
              v-for="(color, index) in colors"
              :key="index"
              :style="{ backgroundColor: color.code }"
              :title="color.name"
              :aria-label="'Mu ' + color.name"
            ></div>
