  <!-- <section
      id="banner"
      class="py-5 text-white mt-3 banner-section animate"
      :style="bannerStyle"
      aria-label="Gii thiu khng gian sng"
    >
    
    </section> -->
</template>

<script>
import { imageUrls } from "@/assets/js/imgUrl.js";
import TuVanFree from "./TuVanFree.vue";

export default {
  name: "LandingBanner",
  components: {
    TuVanFree,
  },
  data() {
    return {
      imageUrls,
      colors: [
        { name: "Hng pastel", code: "#c07bb0" },
        { name: "Xanh bin", code: "#3b6e99" },
        { name: "Xanh l", code: "#8cc04b" },
        { name: "Vng nht", code: "#f3ea4d" },
        { name: "Cam vng", code: "#ffa000" },
        { name: " ti", code: "#ff3300" },
      ],
    };
  },
  computed: {
    bannerStyle() {
      return {
        backgroundImage: `url('${
          this.imageUrls.bannerBG ||
          "https://wallpapers.com/images/featured/plant-background-mh4y9mexexlv960o.jpg"
        }')`,
        backgroundSize: "cover",
        backgroundPosition: "center",
        backgroundRepeat: "no-repeat",
        height: "450px",
      };
    },
  },
  mounted() {
    const observer = new IntersectionObserver(
      (entries) => {
        entries.forEach((entry) => {
          if (entry.isIntersecting) {
            entry.target.classList.add("show");
            observer.unobserve(entry.target); // ch animate 1 ln
          }
        });
      },
      { threshold: 0.2 }
    );

    document.querySelectorAll(".animate").forEach((el) => observer.observe(el));
  },
};
</script>

<style scoped>
.animate {
  opacity: 0;
  transform: translateY(30px);
  transition: all 0.8s ease;
}
.gradient-text {
  background: linear-gradient(
    to right,
    #e53935,
    #fb8c00,
    #fdd835,
    #43a047,
    #1e88e5
  );
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-weight: bold;
}
/* Khi hin ra */
.animate.show {
  opacity: 1;
  transform: translateY(0);
}

/* Hiu ng fade-right */
.animate[data-animate="fade-right"] {
  transform: translateX(-40px);
}
.animate.show[data-animate="fade-right"] {
  transform: translateX(0);
}

/* Hiu ng fade-left */
.animate[data-animate="fade-left"] {
  transform: translateX(40px);
}
.animate.show[data-animate="fade-left"] {
  transform: translateX(0);
}

/* Hiu ng fade-up */
.animate[data-animate="fade-up"] {
  transform: translateY(40px);
}
.animate.show[data-animate="fade-up"] {
  transform: translateY(0);
}

/* Hiu ng zoom-in */
.animate[data-animate="zoom-in"] {
  transform: scale(0.8);
}
.animate.show[data-animate="zoom-in"] {
  transform: scale(1);
}
/* Tiu  */
.banner-heading {
  text-align: left;
  margin-top: 10px;
}
.banner-heading .highlight {
  font-size: 2.8rem;
  font-weight: bold;
  color: #d4ff00;
  font-family: "Pacifico", cursive;
  display: block;
}
.banner-heading .subtext {
  font-size: 2rem;
  font-weight: 600;
  color: #ffffff;
  font-family: "Montserrat", sans-serif;
  display: block;
}

/* B su tp lc gic */
.hexagon-gallery {
  display: grid;
  grid-template-columns: repeat(3, 100px);
  grid-template-rows: repeat(2, 100px);
  gap: 70px 50px;
  justify-content: center;
  align-items: center;
}

.hexagon-gallery .hex:nth-child(1),
.hexagon-gallery .hex:nth-child(5) {
  grid-row: 1;
}
.hexagon-gallery .hex:nth-child(2),
.hexagon-gallery .hex:nth-child(4) {
  grid-row: 2;
}
.hexagon-gallery .hex:nth-child(3) {
  grid-row: 1 / span 2;
  grid-column: 2;
  align-self: center;
}

/* Style nh lc gic */
.hex {
  width: 150px;
  height: 150px;
  clip-path: polygon(
    30% 0%,
    70% 0%,
    100% 30%,
    100% 70%,
    70% 100%,
    30% 100%,
    0% 70%,
    0% 30%
  );
  background-color: #fff; /* chnh l vin */
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 4px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
}

.hex-inner {
  width: 100%;
  height: 100%;
  clip-path: inherit; /* gi nguyn hnh bt gic bn trong */
  overflow: hidden;
}

.hex-inner img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
  transition: transform 0.3s ease;
}
.hex-inner img:hover {
  transform: scale(1.05);
}

.vinalux-section {
  padding: 40px 0;
}
.vinalux-left {
  text-align: center;
}
.vinalux-img {
  width: 100%;
  max-width: 450px;
  border-radius: 12px;
  box-shadow: 0 6px 24px rgba(0, 0, 0, 0.1);
}

.vinalux-right {
  padding: 20px 30px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.vinalux-title {
  font-size: 28px;
  font-weight: 700;
  color: #0039c7;
  margin-bottom: 16px;
  text-transform: uppercase;
}

.vinalux-desc {
  font-size: 16px;
  color: #333;
  line-height: 1.6;
  margin-bottom: 18px;
}

.vinalux-color-list {
