import { defineStore } from "pinia";


export const useAuthStore = defineStore('auth', {
    state: () => ({
      user: null
    }),
    actions: {
      login(userData) {
        this.user = userData;
      },
      logout() {
        this.user = null;
      }
    },
    persist: {
        enabled: true, //  Kch hot lu tr
        strategies: [
          {
            key: 'auth', // t tn key lu tr
            storage: localStorage, // Hoc sessionStorage nu ch mun lu trong phin lm vic
          }
        ]
      } // Gi trng thi sau khi ti li trang
  });
